
:root {
    --color-primary:#651fff ;
    --color-primary-light: #834bff ;
    --color-primary-dark: #4615b2 ;

    --color-grey-light-1: #faf9f9 ;
    --color-grey-light-2: #f4f2f2 ;
    --color-grey-light-3: #f0eeee ;
    --color-grey-light-4: #ccc ;
    
    --color-grey-dark-1: #333 ;
    --color-grey-dark-2: #777 ;
    --color-grey-dark-3: #999 ;

    --shadow-dark: 0 2rem 6rem rgba(0, 0, 0, .3);
    --shadow-light: 0 2rem 5rem rgba(0,0,0,.06);

    --line: 1px solid var(--color-grey-light-2);
}


$bp-largest: 75em;        // 1200px/16 = 75em     // bp -> breaking point for media queries
$bp-large: 68.75em;       // 1100px/16 = 68.75em
$bp-medium: 56.25em;      // 900px/16  = 56.25em
$bp-small: 37.5em;        // 600px/16  = 37.5em
$bp-smallest: 31.25em;    // 500px/16  = 31.25em


* {
    margin: 0;
    padding: 0;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-size: 62.5% ; // 1rem = 10px,  10px/(16px)->'default browser font size' = 62.5%

    @media only screen and (max-width: $bp-large) {
        font-size: 50%; // 1rem = 8px
    }
}

body {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    line-height: 1.6;
    color: var(--color-grey-dark-2);
    background-image: linear-gradient(to right bottom, var(--color-primary-light), var(--color-primary-dark));
    background-size: cover;
    background-repeat: no-repeat;

    min-height: 100vh;
}